.input-box .label {
  color: #333;
  padding-right: 0;
  width: 90px;
  /* text-align: center; */
  font-size: 0.8rem;
  padding-left: 15px;
}
.ocps-add-card {
  height: 100px;
  border-radius: 5px;
  border: 1px dashed #A6A6A6;
  text-align: center;
  width: 90%;
  margin: 0 auto;
}
.add {
  font-size: 2rem;
  color: #555555;
  margin-top: 0.5rem;
}
/*转账附卡*/
.ocpspc-transferCard-box{
  width: 45%;
  margin: 0 auto;
}
.bg-transferCard {
  height: 120px;
}
.cops-bank-balance {
  text-align: right;
}
.ocpspc-transferCard-box .ocps-transferCard-rt{
  height: auto;
}
.ocpspc-transferCard-box .ocps-transferCard-bg{
  width: 100%;
}
/**********************************************手续费率************************************************/
.ocps-CommissionRate {
  margin: 10px auto;
  width: 50%;
}
.ocps-CommissionRate li {
  border-bottom: 1px solid #D1E2FF;
  overflow: hidden;
}
.ocps-CommissionRate_lf, .ocps-CommissionRate_rt {
  /* height: 1.75rem; */
  line-height: 1.75rem;
   /*text-align: right;*/
   /*padding-right: 1.3rem;*/
}

.ocps-CommissionRate_lf{
  width: 30%;
  padding-left:20px ;
  text-align: left;
}
.ocps-CommissionRate_rt{
  width: 50%;
}
.ocps-pc-page{
  text-align: right;
  padding-right:10px ;
  margin-top: 10px;
}
.ocps-pc-page>span{
  padding-right:10px ;
  cursor: pointer;
}
/*交易查询*/
.ocps-data-box{
  position: relative;
}
.ocps-data{
  position: absolute;
  right: 0;
  top: 0;
}
.ocps-line-height{
  height: 35px;
}
.ocps-data span{
  display: inline-block;
  cursor: pointer;
  padding: 0 10px;
}



